.todo {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin-bottom: 15px;
    font-size: 1.5rem;
    border-radius: 5px;
    border: 2px solid #555;
    color: #112d49;
    background-color: #fbfef9;
    user-select: none;
  }
  .todo:last-child{
    margin-bottom: 0;
  }
  
  .todoText {
    width: 100%;
    text-align: left;
  }
  

  .todoIcon{
    font-size: 1.8rem;
    margin-right: 10px;
    color: teal
  }